$base64-png: 'image/png;base64';
$base64-jpg: 'image/jpeg;base64';
$base64-svg: 'image/svg+xml;base64';

// font var
//-----------------------------------------------------------------------------------


@include font-face('Montserrat', '../fonts/Montserrat-Light','Montserrat-Light', 300);
@include font-face('Montserrat', '../fonts/Montserrat-Regular','Montserrat-Regular', 400);
@include font-face('Montserrat', '../fonts/Montserrat-Medium','Montserrat-Medium', 500);
@include font-face('Montserrat', '../fonts/Montserrat-Bold','Montserrat-Bold', 700);

@include font-face('Oswald', '../fonts/Oswald-ExtraLight','Oswald-ExtraLight', 200);
@include font-face('Oswald', '../fonts/Oswald-Light','Oswald-Light', 300);

@include font-face('PFDin', '../fonts/PFDinTextCompPro-Thin','PFDinTextCompPro-Thin', 100);
@include font-face('PFDin', '../fonts/PFDinTextCompPro-Regular','PFDinTextCompPro-Regular', 400);




$default-font: Arial, 'Helvetica Neue', Helvetica, sans-serif;
$montserrat: 'Montserrat', $default-font;
$oswald: 'Oswald', $default-font;
$pfdin: 'PFDin', $default-font;

// selection color
//-----------------------------------------------------------------------------------
$color-selection: dodgerblue;

// color var
//-----------------------------------------------------------------------------------
$color-link: dodgerblue;
$black: #262626;
$white: #ffffff;
$brown: #cab486;
$darkgrey: #484848;

$mobile-xs: 480px;
$mobile: 640px;
$tablet: 768px;
$medium: 1024px;
$desctop-xs: 1200px;
$desctop: 1440px;
$desctop-xl: 1680px;
