// header
//-----------------------------------------------------------------------------------
.header {
  position: absolute;
  top: 0;
  z-index: 10;
  width: 100%;
  &.header-fixed {
    position: fixed;
    left: 0;
    background-color: #282828;
    z-index: 100;
    .header-wrapper {
      padding: 20px 0;
      @media (max-width: 767px) {
        padding: 10px 0; } } } }


.header-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 50px 0;
  @media (max-width: $mobile-xs) {
    padding: 30px 0; }
  .el-social {
    flex-grow: 1;
    padding-left: 20px;
    @media (max-width: 1080px) {
      display: none; } }

  .el-social.mod-grey a {
 }    //border: none
  .header-contacts {
    display: flex;
    align-items: center;
    font-family: $montserrat;
    font-size: 14px;
    line-height: 14px;
    color: $brown; }
  .header-phone {
    transition: all 0.2s;
    margin-right: 30px;
    @media (max-width: $medium) {
      display: none; }
    &:hover {
      color: white;
      text-decoration: none; }
    &:active {
      color: rgba(255, 255, 255, 0.3); } }
  .header-call {
    margin-right: 30px;
    @media (max-width: $medium) {
      display: none; } }


  .header-lang {
    margin-right: 30px;
    position: relative;
    &.mod-ru {
      .de {
        display: none; } }
    &.mod-de {
      .de {
        position: static;
        display: inline-block; }
      .ru {
        display: none; } }

    &.is-active {
      .ru {
        background: $brown;
        color: white; }
      .de {
        display: inline-block; } }
    .de {
      position: absolute;
      top: 49px;
      left: 0;
      display: none;
      @media (max-width: $mobile-xs) {
        top: 39px; } }
    @media (max-width: $mobile-xs) {
      margin-right: 9px; } }

  .header-menu-btn {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      width: 26px;
      height: 1px;
      left: 50%;
      top: 24px;
      margin-left: -13px;
      background-color: $brown;
      box-shadow: 0 9px 0 0 $brown, 0 -9px 0 0 $brown;
      @media (max-width: $mobile-xs) {
        width: 20px;
        top: 18px;
        margin-left: -10px;
        box-shadow: 0 7px 0 0 $brown, 0 -7px 0 0; } }
    &:hover {
      background-color: $brown;
      &::before {
        background-color: $white;
        box-shadow: 0 9px 0 0 $white, 0 -9px 0 0 $white; } }
    &:active {
      background-color: #202323;
      border: solid 1px #202323;
      color: rgba(255, 255, 255, 0.25);
      &::before {
        background-color: rgba(255, 255, 255, 0.25);
        box-shadow: 0 9px 0 0 rgba(255, 255, 255, 0.25), 0 -9px 0 0 rgba(255, 255, 255, 0.25); } } } }




// global menu
//-----------------------------------------------------------------------------------
body.open-general-menu {
  overflow: hidden;
  .global-menu {
    transform: translateX(0); } }

.global-menu {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  background-color: white;
  background-image: url(../img/general-menu-bg.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-y: auto;
  transition: transform 0.4s;
  transform: translateX(-100%);

  .menu-content {
    height: auto;
    min-height: 100%;
    width: 100%;
    max-width: 1360px;
    padding: 50px 100px 50px 150px;
    background-color: rgba(#2a2e30, 0.85);

    @media (max-width: 1500px) {
      max-width: calc(100% - 160px); }
    @media (max-width: 992px) {
      padding: 50px 50px 50px 50px; }

    @media (max-width: 767px) {
      max-width: 100%; }
    @media (max-width: 580px) {
      padding: 20px; } }

  .btn-close-menu {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    transition: all 0.2s;
    background-color: white;
    width: 150px;
    height: 160px;
    background-image: url(../img/svg/close-menu.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 23px;
    @media (max-width: 767px) {
      width: 50px;
      height: 60px; }
    &:hover {
      background-color: $brown;
      background-image: url(../img/svg/close-menu-w.svg); }
    &:active {
      background-color: #202323;
      background-image: url(../img/svg/close-menu-w.svg); } }

  .list-menu-wrap {
    @media (max-width: 580px) {
      padding-right: 30px; }
    li {
      position: relative;
      &:before {
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        display: inline-block;
        content: '';
        width: 40px;
        opacity: 0;
        transition: all 0.3s;
        height: 1px;
        background-image: linear-gradient(to right, #c19b76, rgba(#c7b185, 0)); }

      &:not(:last-child) {
        margin-bottom: 40px;
        @media (max-height: 900px) {
          margin-bottom: 10px; } }
      &:nth-child(7) {
        margin-top: 90px;
        @media (max-height: 900px) {
          margin-top: 40px; } }

      &.is-active, &:hover {
        &:before {
          opacity: 1; }
        a {
          color: $brown;
          transform: translateX(70px); } }
      &:active {
        color: #202323;
        &:before {
          opacity: 0.2; }
        a {
          color: #202323;
          transform: translateX(70px); } } }
    a {
      font-family: $oswald;
      display: inline-block;
      font-size: 24px;
      font-weight: 300;
      letter-spacing: 3.6px;
      text-align: left;
      color: #ffffff;
      transition: all 0.2s;
      @media (max-width: 767px) {
        font-size: 20px; }
      &.mod-md {
        font-size: 30px;
        letter-spacing: 4.5px;
        @media (max-width: 767px) {
          font-size: 24px; } }
      &:hover {
        text-decoration: none;
        color: $brown; }
      &:active {
        color: #202323; } } }


  .menu-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 95px;
    @media (max-height: 995px) {
      margin-bottom: 50px; }
    @media (max-width: 580px) {
      flex-direction: column; }

    .el-social {
      margin-left: 70px;
      @media (max-width: 767px) {
        margin-left: 20px; }
      @media (max-width: 580px) {
        margin-left: 0;
        margin-top: 20px; }
      a {
        transition: all 0.2s;
        background-color: rgba(white, 0.1);
        border: 1px solid transparent;
        &:active {
          background-color: #1a1a1a;
          border: 1px solid $brown;
          svg {
            fill: $brown; } }
        &:hover {
          border: 1px solid $brown;
          svg {
            fill: $brown; } }
        svg {
          fill: rgba(white, 0.5); } } } }

  .menu-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 95px;
    @media (max-height: 995px) {
      margin-top: 70px; }
    @media (max-width: 580px) {
      flex-direction: column; }
    .el-btn {
      margin-right: 20px;
      @media (max-width: 580px) {
        margin-right: 0;
        margin-bottom: 20px; } }

    .header-phone {
      font-family: $montserrat;
      font-size: 14px;
      font-weight: normal;
      line-height: 1;
      color: $brown;
      transition: all 0.2s;
      &:hover {
        color: white;
        text-decoration: none; }
      &:active {
        color: rgba(255, 255, 255, 0.3); } } } }
