.mod-news-page {
  .first-section {
    padding-bottom: 300px;
    @media(max-width: $tablet) {
      padding-bottom: 180px; }
    @media(max-width: $mobile-xs) {
      padding-bottom: 150px; }
    .line-item {
      bottom: 140px;
      @media(max-width: $tablet) {
        height: 50px;
        bottom: 110px; }
      @media(max-width: $mobile-xs) {
        bottom: 90px; } } }
  .img-bg {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center; }
  .section-about {
    background-color: #fcfcfd; } }
.news.mod-news-page {
  .news-wrapper {
    @media(max-width: $tablet) {
      padding: 50px 0; } }
  .news-items {
    max-width: 1486px;
    margin-top: -150px;
    padding-bottom: 30px;
    @media(max-width: $medium) {
      padding-bottom: 30px; }
    @media(max-width: $tablet) {
      margin-top: -100px; } }
  .news-item {
    margin-bottom: 20px;
    min-height: 790px;
    @media(max-width: $desctop) {
      min-height: 620px; }
    @media(max-width: $tablet) {
      min-height: auto; }
    &:nth-of-type(n+4) {
      display: block; }
    &:nth-of-type(3n) {
      margin-right: 0px;
      @media(max-width: $medium) {
        margin-right: 20px; }
      @media(max-width: $tablet) {
        margin-right: 0; } }
    @media(max-width: $medium) {
      width: 50%;
      max-width: 360px;
      &:nth-of-type(n+3) {
        display: block; }
      &:nth-of-type(2n) {
        margin-right: 0;
        max-width: 360px; } }
    @media(max-width: $tablet) {
      width: 100%;
      &:nth-of-type(3n) {
        display: block;
        @media(max-width: $tablet) {
          display: block; } } } }

  .pagination {
    display: block;
    height: 50px;
    margin: 0;
    padding: 0;
    margin: 0 auto;
    text-align: center;
    font-size: 0;
    &::after {
      content: '';
      display: table;
      clear: both; }
    li {
      display: inline-block;
      &:not(:last-child) {
        margin-right: 10px; }
      a {
        width: 50px;
        float: left;
        text-transform: uppercase;
        color: $brown;
        border: 1px solid $brown;
        opacity: 0.4;
        @media(max-width: $mobile-xs) {
          width: 40px; }
        &:active {
          opacity: 1; } }
      .mod-dots {
        border: none;
        letter-spacing: 1.4px; } } } }




