.mod-specific-event {
  .el-title {
    margin-bottom: 27px;
    @media (max-width: $tablet) {
      font-size: 40px;
      line-height: 60px; }
    @media (max-width: $mobile-xs) {
        font-size: 32px;
        line-height: 40px; } }
  .first-section {
    background: $white;
    padding-bottom: 90px;
    @media (max-width: $mobile-xs) {
      padding-bottom: 50px; }
    .img-bg {
      opacity: 1; }
    .announce {
      max-width: 810px; } }
  .specific-event-intro {
    position: relative;
    @media(max-width: $desctop) {
      display: block; } }
  .task {
    width: 42.5%;
    min-height: 753px;
    @media(max-width: $desctop) {
      width: 100%;
      min-height: auto; } }
  .task-title {
    font-family: $pfdin;
    font-size: 48px;
    line-height: 1.25;
    letter-spacing: 4.8px;
    font-weight: 100;
    color: rgba(72, 72, 72, 0.9);
    margin-bottom: 30px;
    padding-top: 200px;
    @media(max-width: $desctop) {
      padding-top: 50px; }
    @media (max-width: $tablet) {
      font-size: 40px;
      line-height: 60px; }
    @media (max-width: $mobile-xs) {
        font-size: 32px;
        line-height: 40px;
        padding-top: 30px;
        margin-bottom: 20px; } }
  .task-text {
    font-family: $montserrat;
    font-size: 14px;
    line-height: 2.14;
    font-weight: normal;
    text-align: left;
    color: $darkgrey;
    @media(max-width: $desctop) {
      margin-bottom: 60px; }
    @media(max-width: $mobile-xs) {
      margin-bottom: 40px;
      line-height: 1.8; } }
  .stats {
    position: absolute;
    right: 0;
    top: 0;
    background-color: #191919;
    width: 51%;
    max-width: 983px;
    padding: 150px 0 10px;
    @media(max-width: $desctop) {
      position: relative;
      width: 100%;
      max-width: 100%;
      padding: 100px 0 10px; }
    @media(max-width: $mobile) {
      padding: 50px 0 0; }
    .partnership-wrapper {
      padding: 0; }
    .partnership-steps {
      display: flex;
      justify-content: space-between;
      width: 100%;
      max-width: 783px;
      padding: 0 50px;
      @media(max-width: $desctop) {
        max-width: 870px;
        padding: 0; }
      @media(max-width: $medium) {
        max-width: 700px; }
      @media(max-width: $tablet) {
        max-width: 580px; }
      @media(max-width: $mobile) {
        display: none; }
      @media(max-width: $mobile) {
        &.mod-circle {
          display: flex;
          justify-content: center; } } }
    .partnership-step {
      min-width: auto;
      width: 33%;
      max-width: 150px;
      padding: 0;
      margin-bottom: 130px;
      @media(max-width: $desctop) {
        margin-bottom: 100px; }
      @media(max-width: $mobile) {
        width: 50%;
        max-width: none;
        margin-bottom: 50px; }
      @media(max-width: $mobile-xs) {
        width: 100%; }
      &:nth-of-type(2n) {
        @media (max-width: $medium) {
          .next-step {
            display: block; } } }
      &.mod-circle {
        @media(max-width: $mobile) {
          display: block; } } }
    .next-step {
      position: absolute;
      right: -41%;
      top: 15px;
      width: 18px;
      height: 18px;
      background-image: url(../img/svg/plus.svg);
      z-index: 2;
      @media(max-width: $desctop) {
        right: -69%; }
      @media(max-width: $medium) {
        right: -45%; }
      @media(max-width: $tablet) {
        right: -26%; } }
    .partnership-steps-img {
      position: relative;
      width: 50px;
      height: 50px;
      margin-bottom: 10px;
      border-radius: 100%; }
    .partnership-img {
      width: 150px;
      height: 150px; }
    .partnership-steps-text {
      font-family: $pfdin;
      font-size: 30px;
      font-weight: 100;
      line-height: 2;
      letter-spacing: 1.5px;
      color: #fcfcfd; }
    .partnership-step-info {
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      margin: auto;
      text-align: center;
      font-family: $pfdin;
      font-size: 36px;
      line-height: 150px;
      letter-spacing: 0.4px;
      color: $white;
      opacity: 0.8;
      font-weight: normal; } } }

//details

.details {
  background-color: $white;
  padding: 60px 0;
  @media(max-width: $tablet) {
    padding: 40px 0; }
  @media(max-width: $mobile-xs) {
    padding: 30px 0; }

  .events-name {
    font-family: $montserrat;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.88;
    color: rgba(72, 72, 72, 0.3);
    margin: 0 auto;
    text-align: center;
    text-transform: uppercase; }
  .el-title {
    color: $darkgrey;
    text-transform: uppercase; }
  .icon-title {
    fill: $brown;
    opacity: 0.5;
    margin-bottom: 40px;
    @media(max-width: $tablet) {
      margin-bottom: 20px; }
    @media(max-width: $mobile-xs) {
      margin-bottom: 10px; } }
  .details-text {
    max-width: 1060px;
    margin: 0 auto;
    font-family: $montserrat;
    font-size: 16px;
    line-height: 1.88;
    font-weight: normal;
    color: $darkgrey;
    @media(max-width: $mobile-xs) {
      line-height: 1.6; } } }

//specific-events-gallery

.specific-events-gallery {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  .specific-events-slider {
    background-color: $white;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    .slider-item {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover; } } }

  //nav-spec-slider

  .nav-spec-slider {
    display: flex;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
    font-family: $pfdin;
    font-size: 30px;
    font-weight: normal;
    line-height: 1.2;
    margin: 0 auto;
    margin-top: auto;
    margin-bottom: 40px;
    letter-spacing: 6px;
    color: rgba(255, 255, 255, 0.5);
    @media(max-width: $medium) {
      font-size: 24px;
      margin-bottom: 20px; }
    @media(max-width: $tablet) {
      font-size: 20px;
      margin-bottom: 10px; }
    @media(max-width: $mobile-xs) {
      letter-spacing: 4px; }
    .current {
      font-size: 50px;
      font-weight: normal;
      letter-spacing: 5px;
      line-height: 0.72;
      color: rgba(255, 255, 255, 0.5);
      transform: translateY(-28px);
      @media(max-width: $medium) {
        transform: translateY(-23px);
        font-size: 40px; }
      @media(max-width: $tablet) {
        transform: translateY(-18px);
        font-size: 30px; }
      @media(max-width: $mobile-xs) {
        letter-spacing: 4px; } } }

 }  //gallery

.gallery {
  background-color: $white;
  padding-top: 40px;
  padding-bottom: 50px;
  @media (max-width: $tablet) {
    padding-top: 20px;
    padding-bottom: 30px; }
  @media (max-width: $mobile-xs) {
    padding-top: 0;
    padding-bottom: 10px; }
  .gallery-title {
    font-family: $oswald;
    font-size: 48px;
    font-weight: 200;
    line-height: 2.08;
    color: rgba(72, 72, 72, 0.9);
    text-align: center;
    margin-bottom: 40px;
    @media (max-width: $tablet) {
      font-size: 40px;
      line-height: 1.5; }
    @media (max-width: $mobile-xs) {
      font-size: 32px;
      line-height: 1.25; } }




  .gallery-items {

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 auto;
    max-width: 100%;

    .gallery-item {
      display: block;
      position: relative;
      width: calc(33.33% - 30px);
      max-width: 520px;
      margin: 0 auto 30px;
      background-clip: border-box;
      .el-btn {
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0px;
        transition: all 0.2s;
        left: 0;
        margin: auto; }
      .el-btn {
        &:hover {
          background-color: $brown;
          color: $white; } }
      .gallery-btn {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -25px;
        margin-left: -36px;
        width: 72px;
        height: 50px;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url(../img/gallery-btn.png); }
      &:hover {
        background-clip: content-box;
        background-color: #000000;
        .el-btn, .gallery-btn {
          display: block;
          opacity: 1; }
        img {
          opacity: 0.5; } }
      &.mod-more {
        &:hover {
          background-clip: content-box;
          background-color: #000000;
          .gallery-img {
            opacity: 1; }
          .items-count {
            opacity: 0.8; } } }
      @media (max-width: 1030px) {
        width: calc(50% - 30px); }
      @media (max-width: $mobile) {
        width: calc(100% - 30px);
        max-width: 100%; }
      &:nth-of-type(n+7) {
        display: none; }
      //      &:nth-of-type(3n)
      //        margin-right: 0
      //        @media (max-width: 1030px)
      //          margin-right: 0px
      //        @media (max-width: $mobile)
      //          width: 100%
      //          margin-right: 0
      //      &:nth-of-type(2n)
      //        @media (max-width: 1030px)
      //          margin-right: 30px
      //
      //        @media(max-width: $mobile)
      //          margin-right: 0
      //          width: 100%


      img,iframe {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        opacity: 1;
        transition: all 0.2s;
        max-height: 347px; }

      iframe {
        @media(max-width: $mobile) {
          height: 368px; }
        @media(max-width: $mobile-xs) {
          height: 201px; } }


      //      &:hover
      //        .items-count
 }      //          visibility: visible

    .items-count {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      background-color: $black;
      opacity: 0.5;
      //visibility: hidden
      p {
        position: absolute;
        text-align: center;
        z-index: 3;
        margin: 0 auto;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        height: 100%;
        //margin-top: -24px
        font-family: $oswald;
        font-size: 48px;
        line-height: 1;
        font-weight: 200;
        color: rgba(255, 255, 255, 0.9);
        @extend %vafix; } } } }

//event-review
.event-review {
  background-color: #fcfcfd;
  padding: 150px 0 70px;
  @media(max-width: $desctop) {
    padding: 70px 0; }
  .event-review-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  @media(max-width: $tablet) {
    padding: 40px 0; }
  @media(max-width: $mobile-xs) {
    padding: 30px 0; }
  .event-review-info {
    width: 63%;
    max-width: 889px;
    @media(max-width: $desctop-xl) {
      width: 55%;
      max-width: none; }
    @media(max-width: $tablet) {
      width: 100%; }
    .el-title {
      letter-spacing: -1px; } }
  .event-review-name {
    font-family: $montserrat;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.88;
    color: rgba(72, 72, 72, 0.3);
    text-transform: uppercase;
    @media (max-width: 768px) {
      line-height: 1.6; } }

  .el-title {
    color: $darkgrey;
    text-transform: uppercase;
    text-align: left;
    margin: 0;
    margin-bottom: 100px;
    position: relative;
    @media (max-width: 768px) {
      font-size: 36px;
      line-height: 1.6;
      margin-bottom: 80px; }
    @media (max-width: 480px) {
      font-size: 26px; }
    &::before {
      content: '';
      position: absolute;
      width: 143px;
      height: 24px;
      bottom: -50px;
      left: 0;
      background-image: url(../img/work.png);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center; } }
  .event-review-text {
    margin: 0 auto;
    font-family: $montserrat;
    font-size: 16px;
    line-height: 1.88;
    font-weight: normal;
    color: $darkgrey;
    padding-right: 20px;
    @media(max-width: $tablet) {
      padding-right: 0; }
    @media(max-width: $mobile-xs) {
      line-height: 1.6; } }
  .event-review-img {
    width: 37%;
    max-width: 600px;
    height: 700px;
    @media(max-width: $desctop-xl) {
      width: 45%; }
    @media(max-width: $tablet) {
      display: none; }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } } }
