.mod-services {
  .el-social {
    a {
      opacity: 0.5; } }
  .first-section {
    padding-bottom: 380px;
    @media(max-width: $tablet) {
      padding-bottom: 250px; }
    @media(max-width: $mobile-xs) {
      padding-bottom: 170px; }
    .img-bg {
      opacity: 0.9; }
    .page-titles {
      font-size: 200px;
      line-height: 1;
      letter-spacing: 20px;
      margin: 110px auto 40px;
      color: $white;
      opacity: 1;
      position: relative;
      z-index: 2;
      @media(max-width: $tablet) {
        font-size: 150px;
        letter-spacing: 18px; }
      @media(max-width: $mobile) {
        font-size: 100px;
        letter-spacing: 10px;
        margin-bottom: 20px;
        margin-top: 120px; }
      @media(max-width: $mobile-xs) {
        margin-top: 50px;
        font-size: 80px;
        letter-spacing: 5px; } }
    .announce {
      font-size: 14px;
      line-height: 2.5;
      letter-spacing: 3.5px;
      text-transform: uppercase;
      position: relative;
      z-index: 2;
      opacity: 1;
      @media(max-width: $tablet) {
        line-height: 2;
        letter-spacing: 3px; }
      @media(max-width: $mobile) {
        line-height: 1.5;
        letter-spacing: 2px; }
      @media(max-width: $mobile-xs) {
        font-size: 12px;
        letter-spacing: 1px; } } }
  .services {
    .services-img {
      width: 50%;
      @media(max-width: $desctop) {
        width: 100%;
        max-width: 100%; } }
    .services-information {
      margin-left: auto;
      margin-right: calc(50% - 585px);
      @media(max-width: $desctop) {
        margin: 0 auto; } }
    .services-name {
      @media(max-width: $desctop) {
        margin: 0 auto;
        text-align: center; } }
    .el-title,.icon-title {
      display: none; }
    &.mod-first {
      .services-wrapper {
        padding-top: 100px; } }
    &.mod-right {
      .services-wrapper {
        margin-bottom: 90px; }
      .services-information {
        margin-left: calc(50% - 675px);
        margin-right: auto;
        @media(max-width: $desctop) {
          margin: 0 auto; } }
      .services-img {
        width: 60%;
        @media(max-width: $desctop) {
          width: 100%;
          max-width: 100%; } }
      .services-name {
        @media(max-width: $desctop) {
          margin: 0 auto;
          text-align: center; } } } } }

