  background-color: black;
  height: auto;
  padding-bottom: 160px;
  box-sizing: border-box;
  padding-top: 150px;
  @media (max-width: 480px) {
    padding-top: 100px;
    padding-bottom: 100px; }

  .img-bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.3;
    pointer-events: none;

