  .page-titles {
    font-family: $pfdin;
    font-size: 48px;
    font-weight: normal;
    line-height: 1.25;
    letter-spacing: 4.8px;
    text-align: center;
    color: rgba(255, 255, 255, 0.9);
    max-width: 980px;
    width: 100%;
    margin: 135px auto 40px;
    position: relative;
    z-index: 2;
    @media (max-width: 768px) {
      font-size: 36px;
      margin: 45px auto 45px; }
    @media (max-width: 480px) {
