.detail-news-content {
  .container-news {
    width: 100%;
    max-width: 1320px;
    margin: 0 auto;
    height: 100%;
    padding: 0 15px;
    position: relative;
    z-index: 3; }

  .general-news-img {
    margin-top: -150px;
    width: 100%;
    @media (max-width: 480px) {
      margin-top: -100px; } } }

.any-news-section {
  background-color: #fcfcfd;
  padding: 70px 0;
  .container-news {
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    height: 100%;
    padding: 0 15px;
    position: relative;
    z-index: 3; } }


.tiny-mce-section {
  padding: 70px 0;
  .content {
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    height: 100%;
    padding: 0 15px;
    position: relative;
    z-index: 3; } }
.mod-news-details {
  background-color: #fcfcfd;
  .news-item-wrapper {
    width: calc(100%/2 - 20px);
    max-width: 500px;
    margin-right: 20px;
    margin-bottom: 55px;
    &:nth-of-type(n+3) {
      display: none; }
    &:nth-of-type(2n) {
      margin-right: 0; }
    @media(max-width: $medium) {
      width: 50%;
      max-width: 350px;
      &:nth-of-type(n+3) {
        display: none; } }
    @media(max-width: $tablet) {
      width: 100%;
      margin-right: 0;
      &:nth-of-type(3n) {
        display: none; }
      &:nth-of-type(2n) {
        margin-right: 0;
        max-width: 452px; }
      &:first-of-type {
        @media(max-width: $tablet) {
          display: none; } } }
    .news-list {
      display: block;
      font-family: $montserrat;
      font-size: 14px;
      line-height: 1.71;
      color: $darkgrey;
      text-align: center;
      text-transform: uppercase;
      margin: 0 auto;
      margin-bottom: 25px;
      &.mod-next {
        color: $brown; } }
    .news-item {
      width: 100%;
      margin: 0;
      max-width: 500px; }
    .news-img {
      max-width: 500px; }
    .news-name {
      @media(max-width: $tablet) {
        margin-bottom: 10px; } } } }
