.mod-contact {
  .first-section {
    padding-bottom: 130px;
    @media (max-width: 480px) {
      padding-bottom: 30px; }
    .announce {
      max-width: 752px; } } }


.contact-page-content {
  padding: 90px 0 70px;
  @media (max-width: 768px) {
    padding: 40px 0 0; }
  .container {
    max-width: 100%;
    padding: 0; } }


.map-wrap {
  height: 460px;
  width: 100%;
  .map-image {
    width: 100%;
    height: 100%; } }
.contact-wrap-page {
  position: relative;
  width: 100%; }

.contact-form-wrap {
  width: 673px;
  background-color: rgba(white, 0.95);
  border: solid 1px rgba(#484848, 0.4);
  position: absolute;
  left: 80px;
  top: 50px;
  padding: 146px 0;
  text-align: center;
  z-index: 1;
  @media (max-width: 1240px) {
    width: 440px;
    left: 50px; }
  @media (max-width: 992px) {
    width: 100%;
    position: static;
    border: none;
    padding: 0;
    margin-bottom: 40px; }
  .contacts-info {
    display: block;
    width: 100%;
    max-width: 245px;
    margin: 0 auto;
    .el-btn {
      @media (max-width: 480px) {
        width: 100%; }
      &:not(:last-child) {
        margin-bottom: 30px; } } }
  .el-title {
    margin-bottom: 50px;
    text-align: center;
    @media (max-width: 768px) {
      font-size: 36px;
      line-height: 1.6;
      margin-bottom: 30px; }
    @media (max-width: 480px) {
      font-size: 26px; } }


  .content-text {
    font-family: $montserrat;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.5;
    text-align: center;
    color: #484848;
    width: 100%;
    margin-bottom: 50px;
    @media (max-width: 768px) {
      font-size: 14px; } } }

.contact-text-wrap {
  padding: 110px 50px 30px 360px;
  margin-left: 460px;
  width: 100%;
  text-align: left;
  max-width: calc(100% - 530px);
  background: url(../img/bg-contact-page.jpg) no-repeat center/cover;
  @media (max-width: 1240px) {
    margin-left: 410px;
    padding-left: 130px;
    max-width: calc(100% - 450px); }
  @media (max-width: 992px) {
    padding-left: 30px;
    margin-left: 0;
    padding-right: 30px;
    max-width: 100%; }
  @media (max-width: 768px) {
    margin-bottom: 30px;
    padding-top: 40px;
    padding-bottom: 30px; }


  .contacts-company {
    font-family: $montserrat;
    color: $white;
    @media (max-width: 768px) {
      margin: 0 auto; } }

  .contacts-title {
    font-family: $oswald;
    font-size: 48px;
    line-height: 80px;
    font-weight: 200;
    color: rgba(255,255,255, 0.9); }
  .items {
    font-size: 14px;
    line-height: 30px;
    margin-top: 60px;
    margin-bottom: 85px;
    @media (max-width: 768px) {
      margin-bottom: 40px;
      margin-top: 20px; } }
  .contacts-city {
    letter-spacing: 3.5px;
    margin-bottom: 35px;
    text-transform: uppercase; }
  .item {
    margin-bottom: 20px;
    span {
      display: inline-block;
      font-size: 16px; }
    .title {
      text-transform: uppercase;
      font-size: 14px;
      min-width: 100px;
      margin-right: 17px;
      color: $brown;
      letter-spacing: 3.5px;
      @media(max-width: $desctop-xl) {
        margin-right: 5px; } } } }
