.mod-corporate-event {
  header {
    .el-social {
      a {
        opacity: 0.5;
        border: none;
        background-color: #2d2d2d; }
      svg {
        fill: rgba(255, 255, 255, 0.3); } } }
  a.look-more-text {
    color: $brown; }
  .first-section {
    padding-bottom: 270px;
    @media (max-width: 768px) {
      padding-bottom: 80px; }
    .line-item {
      bottom: 180px;
      @media (max-width: 768px) {
        height: 50px;
        bottom: 35px; } }
    .section-about {
      @media (max-width: 768px) {
        padding: 40px 0; } }
    .page-titles {
      margin: 140px auto 30px;
      @media (max-width: 768px) {
        font-size: 36px;
        line-height: 1.6;
        margin: 45px auto 45px; }
      @media (max-width: 480px) {
        font-size: 26px;
        margin-bottom: 20px; } } }
  .partnership {
    background-image: none;
    background: $white;
    max-width: 1320px;
    margin: 0 auto;
    margin-top: -100px;
    position: relative;
    z-index: 2;
    @media (max-width: $desctop-xs) {
      margin-top: 0; } }
  .partnership-wrapper {
    padding: 55px 0 110px;
    background: $white;
    .el-title {
      background-color: $white; }
    .icon-title {
      fill: #be9b76;
      opacity: 0.5;
      margin-bottom: 70px;
      @media (max-width: $medium) {
        margin-bottom: 50px; } }
    .text {
      color: $darkgrey; }

 }    //corporate-events
  .corporate-events {
    position: relative;
    overflow: hidden;
    &.mod-first {
      padding-top: 140px;
      @media(max-width: $desctop) {
        padding-top: 70px; }
      @media(max-width: $desctop-xs) {
        padding-top: 0; } } }
  .corporate-events-wrapper {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-bottom: 90px;
    overflow: hidden;
    &:last-of-type {
      padding-bottom: 0; }
    &.mod-left {
      flex-direction: row-reverse;
      .corporate-events-img {
        left: auto;
        right: -12%;
        top: 25%;
        width: 67%;
        max-width: 1340px;
        max-height: 720px;
        margin-top: -280px;
        @media(max-width: $desctop-xl) {
          top: 35%; }
        @media(max-width: $desctop-xl) {
          top: 35%; } }

      .corporate-events-desc {
        padding-top: 35px;
        padding-right: 10px;
        padding-left: 110px;
        width: 33%;
        @media(max-width: $desctop-xl) {
          padding-right: 50px;
          padding-left: 40px; }
        @media(max-width: $desctop) {
          padding-left: 0;
          padding-right: 0; }
        @media(max-width: $desctop-xs) {
          width: 100%; } } }
    &.mod-int {
      flex-direction: row-reverse;
      .corporate-events-img {
        max-height: 400px;
        max-width: 870px;
        width: 67%;
        top: 51%;
        right: 8%;
        @media(max-width: $desctop-xl) {
          right: 0; }
        @media(max-width: $desctop) {
          max-width: 800px;
          top: 55%; } } }
    &.mod-conf {
      .corporate-events-img {
        top: 46%;
        left: 12%;
        max-height: 400px;
        max-width: 740px;
        @media(max-width: $desctop-xl) {
          left: 3%; }
        @media(max-width: $desctop) {
          left: 0%; } } } }
  .corporate-events-img {
    position: absolute;
    top: 57%;
    left: 2%;
    width: 54%;
    margin-top: -265px;
    max-width: 1040px;
    max-height: 530px;
    @media(max-width: $desctop-xl) {
      width: 50%;
      left: -5%; }
    @media(max-width: $desctop) {
      width: 57%;
      left: -6%; }
    @media(max-width: $desctop-xs) {
      display: none; }
    img {
      height: 100%;
      width: 100%;
      object-fit: cover; } }
  .corporate-events-desc {
    width: 37%;
    position: relative;
    z-index: 2;
    min-width: 715px;
    padding-bottom: 70px;
    padding-right: 150px;
    @media(max-width: $desctop-xl) {
      padding-right: 50px; }
    @media(max-width: $desctop) {
      padding-right: 10px;
      min-width: 570px; }
    @media(max-width: $desctop-xs) {
      width: 100%;
      padding-right: 0; }
    @media(max-width: $medium) {
      min-width: auto; }
    @media(max-width: $tablet) {
      padding-bottom: 50px; }
    @media (max-width: $mobile) {
      padding-bottom: 30px; }
    &.mod-first {
      @media(max-width: $desctop-xs) {
        padding-top: 70px; }
      @media(max-width: $tablet) {
        padding-top: 50px; }
      @media (max-width: $mobile) {
        padding-top: 30px; } } }

  .corporate-events-title {
    font-family: $pfdin;
    font-size: 48px;
    line-height: 1.2;
    color: $darkgrey;
    text-transform: uppercase;
    letter-spacing: 3.6px;
    font-weight: 100;
    margin-bottom: 100px;
    @media (max-width: $tablet) {
      margin: 0 auto;
      margin-bottom: 55px;
      text-align: center; }
    @media (max-width: $mobile) {
      font-size: 40px;
      line-height: 48px;
      letter-spacing: 3px;
      margin-bottom: 30px;
      @media (max-width: $mobile-xs) {
        font-size: 24px;
        line-height: 32px;
        letter-spacing: 2px;
        margin-bottom: 25px; } }

    a {
      display: block;
      padding-left: 70px;
      color: $brown;
      position: relative;
      @media(max-width: $tablet) {
        display: inline-block;
        padding-left: 20px; }
      &::before {
        content: '';
        position: absolute;
        width: 143px;
        height: 24px;
        bottom: -70px;
        left: 0;
        background-image: url(../img/work.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        @media(max-width: $tablet) {
          display: none; } } } }
  .corporate-events-text {
    font-family: $montserrat;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.8;
    color: $darkgrey;
    padding-bottom: 50px;
    @media(max-width: $desctop) {
      padding-bottom: 30px; }
    @media (max-width: $mobile) {
      font-size: 16px;
      line-height: 24px;
      padding-bottom: 20px; }
    @media (max-width: $mobile-xs) {
      font-size: 14px;
      line-height: 20px; } }


  .mod-events-pages {
    background-image: none;
    background-color: $white;
    padding: 50px 0 75px;
    .el-title {
      color: rgba(72, 72, 72, 0.9); }
    .icon-title {
      fill: #484848;
      opacity: 0.5;
      margin-bottom: 50px; } }
  .my-work-wrapper {
    padding: 0;
    margin-top: 0;
    max-width: 1920px; }
  .images {
    width: 100%;
    max-width: 1460px;
    margin: 0 auto;
    @media(max-width: $tablet) {
      display: flex;
      flex-direction: column; }
    .img-item {
      background-clip: content-box;
      position: relative;
      transition: all 0.2s;
      .el-btn {
        visibility: hidden;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto; }
      img {
        opacity: 1;
        transition: opacity 0.2s; }
      &:hover {
        background-color: #000000;
        img {
          opacity: 0.5; }
        .el-btn {
          visibility: visible;
          z-index: 3; } } }


    .container1 {
      width: 60%;
      max-width: 880px;
      @media(max-width: $desctop-xs) {
        width: 44%;
        max-width: 460px; }
      @media(max-width: $tablet) {
        width: 100%;
        display: flex;
        max-width: none; } }

    .container2 {
      position: relative;
      max-width: 580px;
      width: 40%;
      height: 790px;
      @media(max-width: $desctop-xs) {
        display: block;
        width: 56%; }
      @media(max-width: $medium) {
        height: 690px; }
      @media(max-width: $tablet) {
        width: 100%;
        max-width: none;
        height: 650px; }
      @media(max-width: $mobile) {
        height: 530px; }
      @media(max-width: $mobile) {
        height: 450px; } }
    .medium.mod-bottom {
      .little {
        @media(max-width: $tablet) {
          margin-right: 0; } }
      @media(max-width: $mobile-xs) {
        display: none; } }
    .medium.mod-top {
      .little {
        @media(max-width: $mobile-xs) {
          margin-right: 0; } } }

    .little {
      position: relative;
      width: 64.5%;
      max-width: 580px;
      height: 390px;
      @media(max-width: $desctop-xs) {
        width: 100%;
        margin-right: 10px; }
      @media(max-width: $medium) {
        height: 340px; }
      @media(max-width: $mobile) {
        height: 265px; }
      @media(max-width: $mobile-xs) {
        height: 225px; }
      &.mod-smaller {
        width: 35.5%;
        max-width: 310px;
        @media(max-width: $desctop-xs) {
          display: none; } } } } }

