
.mod-portfolio {
  .first-section {
    @media (max-width: 768px) {
      padding-bottom: 80px; }
    .line-item {
      @media (max-width: 768px) {
        height: 50px;
        bottom: 35px; } } } }

.first-section {
  position: relative;
  .line-item {
    position: absolute;
    width: 1px;
    height: 80px;
    background-image: linear-gradient(to top, #c19b76, rgba(#c7b185, 0));
    left: 50%;
    bottom: 80px;
    pointer-events: none; } }

.photos-wrap {
  img {
    width: 100%;
    margin: 15px 0;
    transition: all 0.2s;
    &:hover {
      box-shadow: 0px 30px 30px 0 rgba(72, 72, 72, 0.1); }
    &:last-child {
      margin-bottom: 0; } } }

.section-about {
  background-color: #fcfcfd;
  padding: 70px 0;
  @media (max-width: 768px) {
    padding: 40px 0; }

  .text-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    &.mod-right {
      flex-direction: row-reverse;
      .text-wrap-left {
        margin-left: auto;
        @media (max-width: $desctop) {
          padding-left: 20px; }
        @media (max-width: $medium) {
          padding-left: 0; } } }
    .text-wrap-left {
      width: 44%;
      max-width: 662px;
      box-sizing: border-box;
      @media (max-width: $desctop-xl) {
        width: 50%; }
      @media (max-width: 1170px) {
        width: 100%;
        max-width: none; }
      .yel-sub-title {
        font-family: $montserrat;
        font-size: 14px;
        font-weight: normal;
        line-height: 1.71;
        letter-spacing: 0.7px;
        color: $brown;
        text-transform: uppercase;
        margin-bottom: 0px; }

      .el-title {
        font-family: $pfdin;
        font-weight: 100;
        text-transform: uppercase;
        text-align: left;
        margin-bottom: 50px;
        letter-spacing: 3px;
        @media (max-width: 1440px) {
          padding-right: 10px;
          margin-bottom: 30px;
          letter-spacing: 2px;
          font-size: 40px;
          line-height: 60px; }
        @media (max-width: 1170px) {
          padding-right: 0; }
        @media (max-width: 1024px) {
          margin-bottom: 10px; }
        @media (max-width: $tablet) {
          font-size: 40px;
          line-height: 60px; }
        @media (max-width: $mobile-xs) {
            font-size: 32px;
            line-height: 40px; } }

      .content-text {
        font-family: $montserrat;
        font-size: 16px;
        font-weight: normal;
        line-height: 1.5;
        text-align: left;
        color: #484848;
        max-width: 595px;
        @media (max-width: $desctop-xl) {
          padding-right: 20px; }
        @media (max-width: 1170px) {
          max-width: none;
          padding: 0; }
        @media (max-width: 768px) {
          font-size: 14px; } } }

    .wrap-img {
      width: 56%;
      max-width: 900px;
      @media (max-width: $desctop-xl) {
        width: 50%; }
      @media (max-width: 1170px) {
        display: none; }


      img {
        width: 100%;
        box-shadow: 0px 30px 30px 0 rgba(72, 72, 72, 0.1); } } } }

.portfolio-page-content {
  padding: 150px 0 70px;
  @media (max-width: $desctop) {
    padding: 70px 0; }
  @media (max-width: 768px) {
    padding: 40px 0; } }

.section-about-portfolio {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 15px;
  .img-wrap {
    background-color: #e8e8e8;
    width: 38%;
    max-width: 600px;
    @media (max-width: 992px) {
      display: none; }

    img {
      width: 100%;
      box-shadow: 0 30px 30px 0 rgba(72, 72, 72, 0.1); } }

  .text-wrap {
    width: 62%;
    padding-left: 100px;
    box-sizing: border-box;
    text-align: left;
    @media (max-width: 992px) {
      width: 100%;
      padding-left: 0; }

    small {
      font-family: $montserrat;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.6;
      color: rgba(72, 72, 72, 0.3);
      margin-bottom: 12px;
      text-transform: uppercase;
      display: block; }

    .content-text {
      font-family: $montserrat;
      font-size: 16px;
      font-weight: normal;
      line-height: 1.8;
      text-align: left;
      color: #484848;
      max-width: 540px;
      width: 100%;
      margin-bottom: 50px;
      @media (max-width: 768px) {
        font-size: 14px; } } }

  .el-title.mod-separate {
    position: relative;
    margin-bottom: 105px;
    text-align: left;
    text-transform: uppercase;
    @media (max-width: 768px) {
      font-size: 36px;
      line-height: 1.6;
      margin-bottom: 80px; }
    @media (max-width: 480px) {
      font-size: 26px; }
    &:before {
      content: '';
      position: absolute;
      width: 143px;
      height: 24px;
      bottom: -56px;
      left: 0;
      background-image: url(../img/work.png);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      @media (max-width: 768px) {
        bottom: -46px; } } } }
