// elements
//-----------------------------------------------------------------------------------
//container
.container {
  width: 100%;
  max-width: 1650px;
  margin: 0 auto;
  padding: 0 15px;
  @media (max-width: $desctop-xl) {
    max-width: 1440px; }
  @media (max-width: $desctop) {
    max-width: 1200px; }
  @media (max-width: $desctop-xs) {
    max-width: 1100px; }
  @media (max-width: $medium) {
    max-width: 960px; }
  @media (max-width: $tablet) {
    max-width: 700px; }
  @media (max-width: $mobile) {
    max-width: 580px; }
  @media (max-width: $mobile-xs) {
    max-width: 450px;
    padding: 0 10px; }
  &.mod-cards {
    padding: 0; } }

//el-btn
.el-btn {
  display: block;
  width: 243px;
  height: 50px;
  font-family: $montserrat;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  padding: 18px 0;
  letter-spacing: 0.7px;
  transition: all 0.2s;
  @media (max-width: $mobile-xs) {
    width: 200px;
    height: 40px;
    padding: 13px 0; }
  &:active {
    background-color: #202323;
    border: solid 1px #202323;
    color: rgba(255, 255, 255, 0.25); }

  &.mod-transparent-brown {
    text-transform: uppercase;
    color: $brown;
    border: 1px solid $brown;
    &:hover {
      color: $white;
      background-color: $brown; }
    &:active {
      background: #202323;
      border: solid 1px #202323;
      color: rgba(#000, 0.25); } }
  &.mod-transparent-grey {
    display: block;
    text-transform: none;
    color: #4e5052;
    border: 1px solid #4e5052;
    &:focus {
      color: $brown;
      border: 1px solid $brown; } }
  &.mod-brown {
    text-transform: uppercase;
    color: $white;
    background-color: $brown;
    border: 1px solid $brown;

    &:hover {
      color: $brown;
      background-color: transparent; }
    &:active {
      background: none;
      border: solid 1px #202323;
      color: rgba(#000, 0.25); } }
  &.mod-little-btn {
    width: 50px;
    color: $brown;
    border: 1px solid $brown;
    @media (max-width: $mobile-xs) {
      width: 40px; }
    &:hover {
      color: $white;
      background-color: $brown; } } }



//el-logo


.el-logo {
  display: block;
  width: 256px;
  @media (max-width: $mobile-xs) {
    width: 201px; } }

//el-social
.el-social {
  display: flex;
  li {
    margin-right: 20px;
    @media(max-width: $mobile-xs) {
      margin-right: 10px; }
    &:last-of-type {
      margin-right: 0; } }
  a {
    display: block;
    position: relative;
    width: 50px;
    height: 50px;
    transition: all 0.2s;
    text-align: center;
    border: 1px solid $brown;
    @media(max-width: $mobile-xs) {
      width: 40px;
      height: 40px; }
    &:active {
      cursor: pointer;
      background-color: #1a1a1a;
      border: 1px solid $brown;
      svg {
        fill: $brown; } }

    &:hover {
      cursor: pointer;
      background-color: $brown;
      border: 1px solid $brown;
      svg {
        fill: $brown; } }
    svg {
      transition: all 0.2s;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      margin: auto;
      fill: $brown; } }
  &.mod-grey {
    a {
      background-color: rgba(45, 45, 45, 0.5);
      border: none;
      &:hover {
        border: 1px solid $brown; } }
    svg {
      fill: rgba(255, 255, 255, 0.3); } } }

//button look more

.el-look-more {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 2;
  max-width: 360px;
  //width: 235px
  letter-spacing: 0.7px;
  @media(max-width: $mobile-xs) {
    letter-spacing: 0; }
  &.mod-video {
    //width: 295px
    .look-more-text {
      color: $white;
      padding: 18px 20px;
      @media(max-width: $mobile-xs) {
        padding: 13px 20px;
        text-align: left;
        padding-left: 10px; } } }

  &.mod-order {
    width: auto;
    .look-more-text {
      width: auto;
      color: $brown;
      padding: 18px 20px;
      text-align: left;
      padding-left: 20px;
      @media(max-width: $mobile-xs) {
        padding: 13px 20px; } } }
  &:hover {
    .look-more-text {
      border: 1px solid $brown;
      color: $brown;
      padding-left: 40px;
      padding-right: 40px;
      @media(max-width: $desctop) {
        padding-left: 20px;
        padding-right: 20px; }
      @media(max-width: $mobile-xs) {
        padding-left: 25px;
        padding-right: 25px; } } }
  &:active {
    .look-more-button {
      background: #202323;
      &::before, &::after {
        background: rgba(255, 255, 255, 0.25); } }
    .look-more-text {
      border: 1px solid #202323;
      color: rgba(#000, 0.25); } }

  .look-more-button {
    position: relative;
    width: 50px;
    min-width: 50px;
    height: 50px;
    background-color: $brown;
    z-index: 2;
    transition: all 0.2s;
    @media(max-width: $mobile-xs) {
      width: 40px;
      height: 40px;
      min-width: 40px; }
    &::before, &::after {
      content: '';
      position: absolute;
      top: 24px;
      left: 50%;
      margin-left: -9px;
      width: 18px;
      height: 1px;
      background-color: $white;
      z-index: 2;
      transition: all 0.2s;
      @media(max-width: $mobile-xs) {
        top: 19px; } }
    &::after {
      top: 50%;
      left: 33px;
      margin-top: -9px;
      width: 1px;
      height: 18px;
      @media(max-width: $mobile-xs) {
        left: 29px; } } }

  .look-more-text {
    //width: calc(100% - 50px)
    height: 50px;
    font-family: $montserrat;
    font-weight: 400;
    font-size: 14px;
    line-height: 14px;
    text-transform: uppercase;
    color: $brown;
    border: 1px solid transparent;
    text-align: center;
    padding: 18px 20px;
    transition: all 0.2s;
    &.mod-order {
      display: block;
 }      //width: 285px

    @media(max-width: $mobile-xs) {
      //width: calc(100%-40px)
      height: 40px;
      padding: 13px 20px; } } }

//el-title
.el-title {
  font-family: $oswald;
  font-size: 48px;
  line-height: 80px;
  font-weight: 200;
  color: rgba(72, 72, 72, 0.9);
  text-align: center;
  margin-bottom: 7px;
  @media (max-width: $tablet) {
    font-size: 40px;
    line-height: 60px; }
  &.mod-white {
    color: $white;
    opacity: 0.9; }
  @media (max-width: $mobile-xs) {
      font-size: 32px;
      line-height: 40px; } }
.icon-title {
  width: 247px;
  height: 24px;
  display: block;
  margin: 0 auto;
  fill: #484848;
  @media (max-width: $tablet) {
    width: 220px; } }

//breadcrumbs
.breadcrumbs {
  @extend %clear-fix;
  li {
    float: left;
    position: relative;
    font-family: $montserrat;
    font-size: 12px;
    font-weight: normal;
    letter-spacing: 1.2px;
    text-align: left;
    color: #959696;
    text-transform: uppercase;
    @media (max-width: 768px) {
      font-size: 10px;
      line-height: 1.6; }

    a:hover {
      text-decoration: none;
      color: $brown; }
    span {
      color: $brown; }

    &:not(:last-child) {
      margin-right: 14px;
      &:after {
        content: '';
        width: 1px;
        height: 14px;
        background-color: #959696;
        position: absolute;
        right: -7px;
        transform: rotate(-20deg);
        top: 0; } } } }
//title animation
.title-animation {
  opacity: 0;
  @media(max-width: 1024px) {
    opacity: 1; } }

