.tiny-mce-section {
  color: #4e5052;
  font-family: $montserrat;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.8;
  @media (max-width: 768px) {
    font-size: 14px; }
  h1 {
    font-family: $pfdin;
    font-size: 48px;
    font-weight: normal;
    line-height: 1.25;
    letter-spacing: 4.8px;
    margin-bottom: 30px;
    &.title-page {
      color: #ffffff;
      font-size: 144px;
      line-height: 175px;
      margin-bottom: 10px; }
    &.mod-white {
      color: white; } }

  @media (max-width: 767px) {
    h1 {
      font-size: 36px;
      line-height: 46px; } }

  @media (max-width: 991px) {
    h1.title-page {
      font-size: 84px;
      line-height: 85px;
      margin-bottom: 30px; } }

  @media (max-width: 767px) {
    h1.title-page {
      font-size: 46px;
      line-height: 46px;
      margin-bottom: 30px; } }

  h2 {
    font-family: $pfdin;
    font-size: 36px;
    font-weight: 700;
    line-height: 1;
    text-transform: uppercase;
    margin-bottom: 55px;
    letter-spacing: 3px;
    &.mod-white {
      color: white; }
    small {
      display: block;
      margin-top: 20px; } }

  @media (max-width: 767px) {
    h2 {
      margin-bottom: 30px;
      font-size: 24px; } }

  h3 {
    color: black;
    font-family: $pfdin;
    font-size: 18px;
    font-weight: 700;
    line-height: 18px;
    text-transform: uppercase;
    margin-bottom: 30px;
    &.mod-white {
      color: white; } }

  @media (max-width: 767px) {
    h3 {
      margin-bottom: 24px;
      font-size: 14px; } }

  h4 {
    color: black;
    font-family: $pfdin;
    font-size: 16px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
    margin-bottom: 20px;
    &.mod-white {
      color: white; } }

  small {
    color: black;
    font-family: $montserrat;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 10px;
    &.mod-white {
      color: white; } }

  @media (max-width: 767px) {
    small {
      margin-bottom: 15px; } }
  p {
    color: #4e5052;
    font-family: $montserrat;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.8;
    margin: 1em 0;
    @media (max-width: 768px) {
      font-size: 14px; }
    img {
      margin-bottom: 24px;
      @media (max-width: 999px) {
        width: 100%; }
      & + img {
        display: inline-block;
        margin-left: 25px;
        @media (max-width: 999px) {
          margin-left: 0;
          width: 100%; } }

      &.mod-left {
        float: left;
        margin-right: 30px; }
      &.mod-right {
        float: right;
        margin-left: 30px; } } }
  a {
    color: #cab486; }
  ul, ol {
    list-style-position: inside;
    color: #cab486;
    font-family: $montserrat;
    font-size: 16px;
    font-weight: normal;
    line-height: 1.8;
    margin: 1em 0 1em 20px;
    @media (max-width: 768px) {
      font-size: 14px; } }
  ul li a, ol li a {
    color: #cab486; }
  ul li a:visited, ol li a:visited {
    color: #cab486; }
  ul li span, ol li span {
    color: #4e5052; }
  .img-tag {
    position: relative;
    display: inline-block;
    box-shadow: 0 10px 50px rgba(0, 0, 0, 0.05);
    border-radius: 2px;
    background-color: #ffffff;
    margin: 20px 0;
    img {
      margin-top: 0;
      margin-bottom: 0; }
    p {
      position: absolute;
      height: 50px;
      width: 100%;
      bottom: 0;
      left: 0;
      text-align: center;
      line-height: 50px;
      background-color: #ffffff;
      color: #ced3d9;
      font-family: $montserrat;
      font-size: 12px;
      font-weight: 700;
      text-transform: uppercase;
      margin-bottom: 0; } }
  blockquote {
    font-family: $montserrat;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.8;
    text-align: left;
    color: $brown;
    padding: 0px 40px;
    @media (max-width: 768px) {
      font-size: 14px; } }

  blockquote {
    position: relative;
    margin-top: 60px;
    border-radius: 2px;
    &.small-quote {
      border-bottom: 2px solid #cab486;
      padding-bottom: 60px;
      h3 {
        padding: 40px 40px 20px; }
      p {
        font-style: normal;
        font-weight: 600;
        margin-bottom: 0; } }
    p {
      color: black;
      font-style: italic;
      font-weight: 400;
      + p {
        padding-top: 0; }
      &:only-child {
        margin-bottom: 0; }

      p {
        font-style: italic;
        text-align: center;
        &:first-child {
          padding-top: 54px; } } }
    + p {
      margin-top: 40px; }
    em {
      color: #ced3d9;
      font: 700 12px/1.35 $montserrat;
      text-transform: uppercase; } } }

