// footer
//-----------------------------------------------------------------------------------
.footer {
  .el-social a {
    &:hover {
      color: white;
      .icon {
        fill: white; } } } }

.footer-wrapper {
  display: flex;
  flex-wrap: wrap;
  font-family: $montserrat;
  font-size: 14px;
  line-height: 14px;
  color: $darkgrey;
  .pages-items {
    display: flex;
    justify-content: flex-start;
    width: 700px;
    flex-wrap: wrap;
    @media (max-width: $desctop) {
      width: 650px; }
    @media (max-width: 1260px) {
      max-width: 500px; }
    @media (max-width: 1024px) {
      display: none; } }

  .pages-item {
    text-transform: uppercase;
    letter-spacing: 1.4px;
    margin-bottom: 40px;
    margin-right: 95px;
    transition: all 0.2s;
    @media (max-width: 1260px) {
      margin-right: 25px; }
    &:hover {
      color: $brown; }
    &:nth-of-type(3n+1) {
      width: 80px; }
    &:nth-of-type(3n+2) {
      width: 260px; }
    &:nth-of-type(3n+3) {
      width: 100px;
      margin-right: 0; }
    &:nth-of-type(n+7) {
      margin-bottom: 0; } }
  .dev {
    font-size: 16px;
    line-height: 30px;
    color: #888787;
    @media (max-width: 1024px) {
      margin-top: 15px;
      display: block;
      flex-basis: 100%;
      text-align: center; } }
  .footer-contacts {
    display: flex;
    margin-left: auto;
    flex-wrap: wrap;
    width: 500px;
    justify-content: flex-end;
    @media (max-width: 1260px) {
      max-width: 470px; }
    @media (max-width: 1024px) {
      justify-content: center;
      max-width: 100%;
      width: 100%; } }
  .el-logo {
    margin-left: 50px;
    @media (max-width: 1260px) {
      margin-left: 20px; }
    @media (max-width: 400px) {
      display: none; } } }


.top-footer-wrapper {
  background-color: white;
  padding: 70px 0;
  @media (max-width: 1024px) {
    padding: 40px 0;
