// index/home page
//-----------------------------------------------------------------------------------



//main-information
.main-information-wrapper {
  position: relative;
  font-family: $montserrat;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  color: $white;
  text-transform: uppercase;
  height: 100vh;
  max-height: 960px;
  min-height: 600px;
  overflow: hidden;
  @media(max-width: $medium) {
    min-height: 500px; }
  @media(max-width: $mobile-xs) {
    min-height: 300px;
    max-height: 400px; }

  .slider-footer-container {
    position: absolute;
    bottom: 100px;
    left: 0;
    right: 0;
    //    width: 100%
    //    max-width: 1650px
    //    margin: 0 auto
    //    padding: 0 15px
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    @media(max-width: $mobile-xs) {
      bottom: 30px; }
    &:before {
      position: absolute;
      left: 50%;
      bottom: -50px;
      transform: translateX(-50%);
      display: inline-block;
      content: '';
      transition: all 0.3s;
      width: 1px;
      height: 97px;
      background-image: linear-gradient(to top, #c19b76, rgba(#c7b185, 0));
      @media(max-width: $mobile-xs) {
        display: none; } } }


  .el-social.mod-grey a {
    border: none; }

  .slider-top {
    height: 100%;
    .slick-list {
      height: 100%; }
    .slick-track {
      height: 100%; } }

  .slider-top-item {
    padding: 100px 0 150px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    @media(max-width: $mobile-xs) {
      padding: 100px 0 100px; } }

  .wrapper-content-slider {
    //    width: 100%
    //    max-width: 1650px
    //    margin: 0 auto
    //    padding: 0 15px
    display: flex;
    height: 100%;
    justify-content: flex-start; } }

.main-information-desc {
  align-self: center;
  margin-top: 65px;
  @media (max-width: $tablet) {
    margin-top: 0;
    width: 100%;
    flex-basis: 100%;
    min-width: 100%; }

  .title {
    line-height: 36px;
    letter-spacing: 3.5px;
    margin-bottom: 5px;
    @media (max-width: $desctop-xs) {
      line-height: 28px;
      margin-bottom: 10px; }
    @media (max-width: $tablet) {
      letter-spacing: 2px; }
    @media (max-width: $mobile-xs) {
      font-size: 12px;
      line-height: 16px;
      margin-bottom: 5px; } }
  .name {
    font-family: $pfdin;
    font-weight: 100;
    font-size: 100px;
    line-height: 100px;
    margin-bottom: 60px;
    letter-spacing: 10px;
    white-space: nowrap;
    @media (max-width: $desctop-xs) {
      font-size: 80px;
      line-height: 80px;
      margin-bottom: 30px; }
    @media (max-width: $tablet) {
      font-size: 72px;
      line-height: 72px;
      letter-spacing: 5px; }
    @media (max-width: $mobile) {
      font-size: 54px;
      line-height: 54px;
      margin-bottom: 20px; }
    @media (max-width: $mobile-xs) {
      font-size: 40px;
      line-height: 40px;
      letter-spacing: 3px;
      margin-bottom: 15px; } } }
//--------------------------add margins


//nav-slider

.nav-slider {
  display: flex;
  font-family: $pfdin;
  font-size: 30px;
  font-weight: 100;
  line-height: 1;
  margin-bottom: -32px;
  letter-spacing: 6px;
  color: rgba(255, 255, 255, 0.3);
  @media(max-width: $mobile-xs) {
    font-size: 24px;
    letter-spacing: 5px; }
  .current {
    font-size: 50px;
    font-weight: normal;
    letter-spacing: 5px;
    color: rgba(255, 255, 255, 0.5);
    transition: all 0.2s;
    transform: translateY(-28px);
    @media(max-width: $mobile-xs) {
      font-size: 40px;
      letter-spacing: 4px; }
    &:hover {
      color: rgba(255, 255, 255, 1); } } }


//services-wrapper
.services {
  position: relative;
  .services-img {
    position: absolute;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    max-width: calc(50% + 80px);
    left: 0;
    @media(max-width: $desctop-xs) {
      max-width: calc(42% + 80px); }
    @media(max-width: 1100px) {
      max-width: 100%; }
    @media (max-width: 768px) {
      opacity: 0.2; } }
  .services-wrapper {
    margin-bottom: 30px;
    padding-top: 50px;
    padding-bottom: 50px; }
  .icon {
    display: block;
    position: relative;
    text-align: center;
    margin: 0 auto 42px;
    width: 247px;
    height: 24px;
    z-index: 2; }
  .el-title {
    position: relative;
    z-index: 2; }
  .el-look-more {
    margin-left: 130px;
    @media(max-width: 1100px) {
      margin: 0 auto; }
    @media (max-width: 768px) {
      margin-top: 30px; } }
  .services-information {
    width: 625px;
    margin-left: 50%;
    @media(max-width: $desctop-xs) {
      margin-left: 42%;
      margin-right: 2%; }
    @media(max-width: 1100px) {
      margin: 0 auto; }
    @media(max-width: $mobile) {
      padding: 0 15px;
      width: 100%;
      max-width: 580px; } }

  .services-name {
    position: relative;
    z-index: 2;
    margin-left: 130px;
    @media(max-width: 1100px) {
      margin: 0;
      text-align: center; }
    p {
      font-family: $montserrat;
      font-weight: 500;
      font-size: 16px;
      line-height: 30px;
      color: rgba(72, 72, 72, 0.3);
      text-transform: uppercase;
      @media (max-width: $mobile) {
        font-size: 14px;
        line-height: 24px; } }
    a {
      display: block;
      font-family: $pfdin;
      font-weight: 100;
      font-size: 48px;
      line-height: 72px;
      margin-bottom: 30px;
      color: #484848;
      text-transform: uppercase;
      letter-spacing: 3.6px;
      @media (max-width: $mobile) {
        font-size: 40px;
        line-height: 60px;
        letter-spacing: 3px; }
      @media(max-width: $mobile-xs) {
        font-size: 32px;
        line-height: 48px;
        letter-spacing: 2px;
        margin-bottom: 20px; } } }

  .services-items {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    position: relative;
    margin-bottom: 25px;
    @media(max-width: $desctop) {
      overflow: hidden; }
    @media(max-width: $mobile) {
      width: 100%;
      max-width: 300px;
      margin: 0 auto; }
    &::before {
      content: '';
      position: absolute;
      right: -55px;
      bottom: 20px;
      width: 18px;
      height: 18px;
      background-image: url(../img/svg/plus.svg);
      z-index: 2; } }
  .services-item {
    position: relative;
    z-index: 2;
    width: 50%;
    max-width: 300px;
    height: 300px;
    padding: 40px 0px;
    background-color: $white;
    margin-bottom: 25px;
    opacity: 0.9;
    box-shadow: 0px 40px 50px 0 rgba(72, 72, 72, 0.2);
    @media(max-width: $tablet) {
      margin-bottom: 20px; }
    @media(max-width: $mobile) {
      width: 300px; }
    @media(max-width: $mobile-xs) {
      width: 100%;
      margin: 10px;
      margin-bottom: 20px;
      height: 280px; }
    &:nth-of-type(2n+1) {
      margin-right: 25px;
      @media(max-width: $tablet) {
        margin-right: 20px; }
      @media(max-width: $mobile) {
        margin-right: 0; }
      @media(max-width: $mobile-xs) {
        margin-right: 10px; } } }


  .services-icon {
    position: relative;
    width: 150px;
    height: 150px;
    line-height: 150px;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 30px;
    .chart {
      position: absolute;
      left: 0;
      top: 0;
      pointer-events: none; }
    img {
      vertical-align: middle; } }
  .services-text {
    font-family: $montserrat;
    font-size: 16px;
    line-height: 24px;
    color: $darkgrey;
    text-align: center;
    span {
      color: $brown; } }
  &.mod-right {
    .services-name {
      margin-left: 0; }
    .el-look-more {
      margin-left: 0;
      @media(max-width: 1100px) {
        margin: 30px auto 0; } }
    .services-items {
      &::before {
        content: '';
        position: absolute;
        left: -150px;
        top: 40%;
        width: 18px;
        height: 18px;
        background-image: url(../img/svg/plus.svg);
        z-index: 2; } }
    .services-img {
      left: auto;
      position: absolute;
      right: 0; }
    .services-information {
      margin-right: 51.5%;
      margin-left: auto;
      margin-top: 85px;
      @media(max-width: $desctop) {
        margin-top: 0; }
      @media(max-width: $desctop-xs) {
        margin-right: 38%;
        margin-left: 6%; }
      @media(max-width: 1100px) {
        margin: 0 auto; }
      @media(max-width: $mobile) {
        width: 100%;
        max-width: 580px; } } } }

//my-work-wrapper
.my-work {
  background-image: url(../img/my-work.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }
.my-work-wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  margin-right: 0;
  max-width: 1820px;
  margin-top: 70px;
  padding: 101px 50px;
  @media (max-width: $desctop-xs) {
    max-width: 1100px;
    padding: 100px 15px; }
  @media (max-width: $medium) {
    max-width: 960px; }
  @media (max-width: $tablet) {
    margin: 0 auto;
    margin-top: 50px;
    padding: 50px 0;
    max-width: 700px;
    flex-direction: column;
    align-items: center; }
  @media (max-width: $mobile) {
    max-width: 580px; }
  @media (max-width: $mobile-xs) {
    max-width: 450px;
    padding: 30px 10px; }
  .el-look-more {
    margin-top: 10px;
    @media (max-width: $tablet) {
      margin-bottom: 30px;
      margin-top: 0; }
    @media (max-width: $mobile) {
      margin-bottom: 20px; } }

  br {
    @media (max-width: $tablet) {
