  .information {
    display: flex;
    flex-direction: column;
    max-width: 490px;
    max-height: 650px;
    padding-right: 50px;
    overflow: auto;
    -ms-overflow-style: none;
    overflow: -moz-scrollbars-none;
    &::-webkit-scrollbar {
      display: none; }
