      margin-top: 20px; } }
  .container1 {
    width: 100%;
    max-width: 716px; }
  .medium {
    display: flex;
    &.mod-top {
      margin-bottom: 10px;
      @media (max-width: $desctop-xs) {
        .mod-smaller {
          padding-left: 10px;
          padding-right: 0; } } } }
  .little {
    padding-right: 10px;
    height: 320px;
    width: 100%;
    max-width: 470px;
    @media (max-width: $desctop-xs) {
      padding-right: 0px; }
    &.mod-smaller {
      width: 100%;
      max-width: 246px;
      @media (max-width: $desctop-xs) {
        padding-right: 10px; }
