  .container2 {
    width: 40%;
    max-width: 472px;
    height: 650px;
    @media (max-width: $desctop-xs) {
      display: none; } }
  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover; } }


//services-form
.services-form {
  background-image: url(../img/form-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }
.services-form-wrapper {
  text-align: center;
  padding: 60px 0;
  @media (max-width: $mobile) {
    padding: 30px 0; }
  .el-title {
    line-height: 60px;
    margin-bottom: 10px;
    @media (max-width: $mobile) {
      font-size: 32px;
      line-height: 40px; } }

  .icon-title {
    fill: $brown;
    margin-bottom: 24px;
    margin-top: 10px;
    opacity: 0.5; }
  p {
    font-family: $montserrat;
    font-size: 14px;
    line-height: 30px;
    color: $brown;
    text-transform: uppercase;
    letter-spacing: 3.5px;
    @media (max-width: $mobile) {
      font-size: 12px;
      line-height: 24px;
      letter-spacing: 2px; } }
  .services-form-info {
    display: flex;
    justify-content: center;
    @media (max-width: $medium) {
      flex-wrap: wrap; }
    .el-btn {
      margin-bottom: 10px;
      letter-spacing: 0;
      @media (max-width: $tablet) {
        width: 200px; }
      @media (max-width: $mobile-xs) {
        width: 100%;
        max-width: 400px;
        margin: 0 auto 10px;
        height: 50px; } }
    button.el-btn {
      letter-spacing: 0.7px;
      @media (max-width: $tablet) {
        font-size: 12px;
        line-height: 1;
        padding: 19px 0; }
      @media (max-width: $mobile-xs) {
        font-size: 14px;
        padding: 18px 0; } }
    input {
      margin-right: 50px;
      border: 1px solid rgba(78, 80, 82, 0.5);
      @media (max-width: $tablet) {
        margin-right: 10px; }
      @media (max-width: $mobile-xs) {
        margin-right: 0; } }
    input::-webkit-input-placeholder {
      font-family: $montserrat;
      font-size: 14px;
      line-height: 14px;
      color: #4e5052; }
    input::-ms-input-placeholder {
      font-family: $montserrat;
      font-size: 14px;
      line-height: 14px;
      color: #4e5052;
      opacity: 1; } } }

//partnership
.partnership {
  background-image: url(../img/partnership.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.partnership-wrapper {
  padding: 42px 0;
  padding-top: 55px;
  .icon-title {
    fill: #ffffff;
    opacity: 0.5;
    margin-bottom: 70px;
    @media (max-width: $medium) {
      margin-bottom: 50px; } }
  .partnership-steps {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    max-width: 1300px;
    margin: 0 auto;
    @media (max-width: $medium) {
      width: 100%; } }
  .partnership-step {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    width: 25%;
    box-sizing: content-box;
    min-width: 300px;
    padding: 0 50px;
    margin-bottom: 20px;
    @media (max-width: $desctop) {
      padding: 0 20px; }
    @media (max-width: $desctop-xs) {
      min-width: 250px; }
    @media (max-width: $medium) {
      box-sizing: border-box;
      width: 50%;
      min-width: none;
      max-width: 380px;
      margin-bottom: 30px; }
    @media (max-width: $mobile) {
      width: 100%;
      max-width: 450px;
      margin-bottom: 50px; }
    .next-step {
      @media (max-width: $mobile) {
        display: none; } }
    &:nth-of-type(3n) {
      .next-step {
        display: none;
        @media (max-width: $medium) {
          display: block; }
        @media (max-width: $mobile) {
          display: none; } } }
    &:nth-of-type(2n) {
      @media (max-width: $medium) {
        .next-step {
          display: none; } } }
    &:last-of-type {
      .next-step {
        display: none; } }
    .next-step {
      position: absolute;
      right: -7.5px;
      top: 93px;
      width: 18px;
      height: 18px;
      background-image: url(../img/svg/plus.svg);
      z-index: 2; } }
  .partnership-img {
    position: relative;
    width: 186px;
    height: 186px;
    margin: 0 auto;
    margin-bottom: 35px;
    border-radius: 50%;
    @media (max-width: $medium) {
      margin-bottom: 30px; }
    img {
      position: absolute;
      margin: auto;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0; } }
  .text {
    font-family: $montserrat;
    font-size: 14px;
    line-height: 24px;
    color: $white;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 0.7px;
    @media (max-width: $mobile) {
      letter-spacing: 0; } } }
//events
