.first-section {
  @media(max-width: $tablet) {
    padding-bottom: 80px; }
  @media(max-width: $mobile-xs) {
    padding-bottom: 30px; } }
.career {
  background-color: #fcfcfd;
  padding: 53px 0;
  .career-items {
    width: 100%;
    max-width: 1008px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    position: relative;
    padding-top: 26px;
    @media(max-width: $tablet) {
      padding-top: 0; }
    &::before {
      content: '';
      position: absolute;
      width: 1px;
      background-color: #c2a984;
      top: 249px;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      @media(max-width: $mobile) {
        left: 0;
        top: 166px; } } }
  .career-img {
    position: relative;
    z-index: 2;
    flex-shrink: 0;
    display: block;
    width: 188px;
    height: 188px;
    align-self: center;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; }
    @media(max-width: $mobile) {
      width: 120px;
      height: 120px; }
    &.mod-first {
      margin-top: 36px;
      @media(max-width: $mobile) {
        margin-bottom: 20px; } }
    &.mod-last {
      @media(max-width: $mobile) {
        display: none; } } }
  .career-item {
    width: 50%;
    @media(max-width: $mobile) {
      width: 100%; }
    &.mod-left {
      align-self: flex-start;
      @media(max-width: $mobile) {
        align-self: center;
        text-align: center; } }
    &.mod-right {
      align-self: flex-end;
      text-align: right;
      @media(max-width: $mobile) {
        align-self: center;
        text-align: center; } } }
  .career-year {
    position: relative;
    font-family: $pfdin;
    font-size: 48px;
    font-weight: normal;
    line-height: 1;
    letter-spacing: 4.8px;
    color: rgba(194, 169, 132, 0.9);
    text-align: center;
    padding-bottom: 20px;
    margin-bottom: 35px;
    @media(max-width: $medium) {
      padding-bottom: 10px;
      margin-bottom: 20px; }
    @media(max-width: $mobile) {
      font-size: 40px;
      line-height: 48px; }
    &::before {
      content: '';
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: #c2a984;
      bottom: 0;
      left: 0;
      right: 0; }
    &::after {
      content: '';
      position: absolute;
      width: 15px;
      height: 15px;
      border-radius: 50%;
      background-color: #c2a984;
      bottom: -7px;
      right: -7px;
      @media(max-width: $mobile) {
        right: auto;
        left: -7px; } }
    &.mod-right {
      &::after {
        right: auto;
        left: -7px;
        @media(max-width: $mobile) {
          left: -7px; } } }
    &.mod-first {
      padding-top: 20px; } }
  .career-text {
    font-family: $montserrat;
    font-size: 18px;
    line-height: 1;
    font-weight: 500;
    color: $darkgrey;
    @media(max-width: $mobile) {
      margin-bottom: 25px;
      line-height: 1.25; }
    span {
      display: block;
      margin-top: 15px;
      font-size: 16px;
      font-weight: normal;
      line-height: 1.13;
      @media(max-width: $medium) {
        margin-top: 10px; }
      @media(max-width: $mobile) {
        line-height: 1.25; } } }
  &.mod-horizontal {
    padding-top: 40px;
    background-color: $white;
    @media(max-width: $desctop-xl) {
      display: none; }
    .el-title {
      margin-bottom: 70px; }
    .career-humor-wrapper {
      display: flex;
      position: relative;
      width: 100%;
      max-width: 1620px;
      @media(max-width: $desctop-xl) {
        flex-direction: column; }
      &::before {
        content: '';
        position: absolute;
        width: calc(100% - 376px);
        max-width: 1246px;
        height: 1px;
        background-color: #c2a984;
        top: 50%;
        bottom: auto;
        left: 188px;
        right: auto;
        transform: translateX(0) translateY(-50%);
        @media(max-width: $desctop-xl) {
          display: none; } } }
    .career-items {
      max-width: 1246px;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-between;
      padding-top: 0;
      &::before {
        display: none; } }
    .career-img {
      margin: 0;
      position: relative;
      z-index: 2;
      flex-shrink: 0;
      &.mod-last {
        margin-left: auto;
        @media(max-width: $desctop-xl) {
          margin: 0; } } }
    .career-item {
      width: 50%;
      display: flex;
      padding: 0;
      margin: 0;
      align-self: flex-start;
      justify-content: flex-start;
      @media(max-width: $desctop-xl) {
        justify-content: center; }
      &.mod-top-first {
        width: 54%;
        max-width: 666px;
        align-self: flex-end;
        @media(max-width: $desctop-xl) {
          width: 100%;
          max-width: 100%; } }
      &.mod-top-last {
        width: 46%;
        padding-left: 0;
        @media(max-width: $desctop-xl) {
          width: 100%;
          max-width: 100%; } }
      &.mod-bottom-first {
        width: 40%;
        max-width: 505px;
        @media(max-width: $desctop-xl) {
          width: 100%;
          max-width: 100%; } }
      &.mod-bottom-last {
        width: 60%;
        padding-right: 90px;
        @media(max-width: $desctop-xl) {
          width: 100%;
          max-width: 100%;
          padding-right: 0; } } }
    .career-year {
      margin-bottom: 0px;
      padding-bottom: 85px;
      padding-top: 85px;
      padding-right: 30px;
      margin-left: auto;
      @media(max-width: $desctop-xl) {
        width: 50%;
        text-align: right;
        margin-left: auto;
        margin: 0;
        padding-top: 70px;
        padding-bottom: 70px;
        padding-right: 100px; }
      &::before {
        width: 1px;
        height: 100%;
        bottom: 0;
        top: 0;
        left: auto;
        right: 0; }
      &::after {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background-color: #c2a984;
        bottom: -7px;
        right: -7px; }
      &.mod-bottom {
        &::after {
          bottom: auto;
          top: -7px; } } }
    .career-text {
      font-size: 18px;
      line-height: 1.33;
      margin-left: 30px;
      @media(max-width: $desctop-xl) {
        width: 50%;
        margin-left: auto;
        font-size: 24px;
        align-self: center;
        padding-left: 100px; }
      @media(max-width: $mobile) {
        font-size: 14px; }
      &.mod-bottom {
        margin-top: auto;
        @media(max-width: $desctop-xl) {
          margin-top: 0; } }
      span {
        font-size: 16px;
        margin-top: 12px;
        line-height: 1.5;
        @media(max-width: $desctop-xl) {
          font-size: 20px; }
        &:last-of-type {
          margin-top: 0; }
        @media(max-width: $mobile) {
          font-size: 14px; } } } }

  &.mod-adaptive {
    display: none;
    @media(max-width: $desctop-xl) {
      display: block;
      background-color: $white;
      .career-items {
        &::before {
          bottom: 187px;
          @media(max-width: $mobile) {
            bottom: 0; } } } } }
  &.mod-dance {
    padding-top: 20px; }
 }  //gallery
.mod-biography {
  .announce {
    max-width: 810px; }
  .gallery-item {
    max-height: 347px;
    background-clip: content-box;
    &:nth-of-type(n+4) {
      display: none; }
    @media(max-width: 1030px) {
      &:nth-of-type(1) {
        display: none; } } }
  .el-title {
    text-transform: uppercase;
    margin-bottom: 27px; }
  .details {
    padding-bottom: 30px; }
  .details-text {
    max-width: 1020px;
    padding-bottom: 30px; }
  .gallery {
    &.mod-last {
      background-color: #fcfcfd; } } }
